<template>
<div>
  <p class="accout-set-title">賬戶設置</p>
  <div class="account-main-container fontSize-14">
    <div class="account-setItem-container flex flex-align-center">
      <div class="flex">
        <p>賬戶密碼：</p>
          <fix-password-diaglog></fix-password-diaglog>
      </div>

    </div>
    <div class="account-setItem-container flex flex-align-center" style="display: none">
      <div>
        <div class="" style="float: left;">
          <p>安全手機</p>
          <p class="theme-fontColor">未綁定</p>
        </div>
         <binding-phone-diaglog></binding-phone-diaglog>
      </div>
    </div>
    <div class="account-setItem-container flex flex-align-center">
      <div>
        <div class="" style="float: left;">
          <p>提幣密碼</p>
          <p class="theme-fontColor">{{ isHaveCapitalPass ? '已設置' : '未設置'}}</p>
        </div>
        <set-canpass-diaglog :isHaveCapitalPass="isHaveCapitalPass" @listenFixPassSuccess="listenFixPassSuccess"></set-canpass-diaglog>
      </div>
    </div>
    <!--<div class="account-setItem-container flex flex-align-center">-->
      <!--<div>-->
        <!--<div class="" style="float: left;">-->
          <!--<p>邮箱账号</p>-->
          <!--<p class="theme-fontColor">{{ userEmail !== null ? userEmail : '未设置'}}</p>-->
        <!--</div>-->
        <!--<fix-email-diaglog :userEmail="userEmail" @listenFixPassSuccess="listenFixPassSuccess"></fix-email-diaglog>-->
      <!--</div>-->
    <!--</div>-->
  </div>

</div>
</template>

<script>
    import axios from 'axios'
    import fixPasswordDiaglog from '../diaglog/fixPasswordDiaglog.vue'
    import bindingPhoneDiaglog from '../diaglog/bindingPhone.vue'
    import setCanpassDiaglog from '../diaglog/setCanPass.vue'
    import fixEmailDiaglog from '../diaglog/fixEmailAccount.vue'
    export default {
      data () {
        return {
          userEmail: '',
          isHaveCapitalPass: false,
          isFixSuccess: false
        }
      },
      computedUserEmail () {
        if (this.userEmail === '' || this.userEmail === null) {
          return this.userEmail = '未設置'
        } else {
          return this.userEmail
        }
      },
      watch: {
        isFixSucess (newdata) {
          this.isFixSuccess = newdata
          this.listenFixPassSuccess()
          return
        }
      },
      components: {
        fixPasswordDiaglog,
        bindingPhoneDiaglog,
        setCanpassDiaglog,
        fixEmailDiaglog
      },
      mounted () {
        this.getUserIsBindingAccountData()
        this.listenFixPassSuccess()
      },
      methods: {
        // 获取用户是否绑定邮箱或者资金账户密码接口请求
        getUserIsBindingAccountData () {
          let vm = this
          axios.get('/promo/authed/account/can/status')
            .then(function (response) {
              vm.userEmail = response.data.email
              vm.isHaveCapitalPass = response.data.isHaveCapitalPass
            })
            .catch(function (error) {
              console.log(error)
            })
        },
        listenFixPassSuccess (msg) {
          this.isFixSuccess = msg
          let vm = this
          if (vm.isFixSuccess) {
            axios.get('/promo/authed/account/can/status')
              .then(function (response) {
                vm.userEmail = response.data.email
                vm.isHaveCapitalPass = response.data.isHaveCapitalPass
              })
              .catch(function (error) {
                console.log(error)
              })
          }
          return
        }
      }
    }
</script>

<style scoped>
.accout-set-title {
  padding-bottom: 24px;
}
.account-main-container>div {
  height: 88px;
  line-height: 22px;
  box-shadow: inset 0 -1px 0 0 rgba(0,0,0,0.10);
  position: relative;
}
</style>



